import { Widget } from '@lumino/widgets';
import { IDefaultFileBrowser } from '@jupyterlab/filebrowser';
import { requestAPI } from './handler';
import { JupyterFrontEnd } from '@jupyterlab/application';
import { createElementWithClass } from './utils';
import { Workspace, fetchWorkspaces } from './workspaceUtils';

export class NDPWidget extends Widget {
  private spinner: HTMLElement;
  private dropdownWorkspaces: HTMLSelectElement;
  private dropdownDatasets: HTMLSelectElement;
  private fileBrowser: IDefaultFileBrowser;
  private pathDisplay: HTMLElement;
  private spinnerContainer: HTMLElement;
  private pathDisplayTitle: HTMLElement;
  private pathDisplayContainer: HTMLElement;
  private workspaces: Workspace[] = [];

  constructor(
    defaultBrowser: IDefaultFileBrowser,
    private commands: JupyterFrontEnd['commands']
  ) {
    super();
    this.fileBrowser = defaultBrowser;

    this.addClass('jp-example-view');
    this.id = 'ndp-widget';
    this.title.label = 'NDP';
    this.title.closable = true;

    const ndpLink = this.createNDPLogo();

    defaultBrowser.model.pathChanged.connect((sender, args) => {
      const currentPath = this.fileBrowser.model.path;
      this.updatePathDisplay(currentPath);
    });

    const buttonFileManager = this.createButton(
      'File Manager',
      this._onGoToFileManagerButtonClick.bind(this)
    );
    const buttonGitExtension = this.createButton(
      'GIT Extension',
      this._onGoToGitExtensionButtonClick.bind(this)
    );
    const buttonDownload = this.createButton(
      'Download Selected Dataset',
      this._onButtonDownloadClick.bind(this)
    );
    const buttonInstall = this.createButton(
      'Install requirements.txt',
      this._onButtonInstallClick.bind(this)
    );
    const buttonGetWorkspacesData = this.createButton(
      'Refresh',
      this._onButtonGetWorkspacesDataClick.bind(this)
    );

    this.dropdownWorkspaces = createElementWithClass(
      'select',
      'ndp-dropdown'
    ) as HTMLSelectElement;
    this.dropdownDatasets = createElementWithClass(
      'select',
      'ndp-dropdown'
    ) as HTMLSelectElement;

    this.spinnerContainer = createElementWithClass('div', 'spinner-container');
    this.spinner = createElementWithClass('div', 'spinner');
    this.spinner.style.display = 'none';

    this.pathDisplayContainer = createElementWithClass(
      'div',
      'path-display-container'
    );
    this.pathDisplayTitle = createElementWithClass('div', 'path-display-title');
    this.pathDisplayTitle.textContent = 'Current Folder:';
    this.pathDisplay = createElementWithClass('div', 'path-display-path');

    this.spinnerContainer.appendChild(this.spinner);
    this.pathDisplayContainer.addEventListener(
      'click',
      this._onGoToFileManagerButtonClick.bind(this)
    );

    this.updatePathDisplay();

    const container = this.createContainer([
      this.createHeader('Utilities'),
      buttonFileManager,
      buttonGitExtension,
      buttonInstall,
      this.createHeader('My Current Workspace'),
      this.dropdownWorkspaces,
      buttonGetWorkspacesData,
      this.createHeader('Items in the Current Workspace'),
      this.dropdownDatasets,
      buttonDownload
    ]);

    this.node.appendChild(ndpLink);
    this.node.appendChild(container);
    this.node.appendChild(this.spinnerContainer);
    this.pathDisplayContainer.appendChild(this.pathDisplayTitle);
    this.pathDisplayContainer.appendChild(this.pathDisplay);
    this.node.appendChild(this.pathDisplayContainer);

    this._getWorkspacesData();

    this.dropdownWorkspaces.addEventListener('change', () => {
      this.updateDatasetsDropdown();
    });
  }

  private createNDPLogo(): HTMLAnchorElement {
    const ndpMapImg = createElementWithClass(
      'img',
      'ndp-logo-map'
    ) as HTMLImageElement;
    ndpMapImg.src = '../style/ndp-landing-image.png';
    const ndpLettersImg = createElementWithClass(
      'img',
      'ndp-logo'
    ) as HTMLImageElement;
    ndpLettersImg.src = '../style/ndp_logo.png';
    const ndpLink = createElementWithClass('a', '') as HTMLAnchorElement;
    ndpLink.href = 'https://ndp-test.sdsc.edu';
    ndpLink.target = '_blank';
    ndpLink.appendChild(ndpMapImg);
    ndpLink.appendChild(ndpLettersImg);
    return ndpLink;
  }

  private createButton(
    text: string,
    onClick: (event: Event) => void
  ): HTMLButtonElement {
    const button = createElementWithClass(
      'button',
      'ndp-button'
    ) as HTMLButtonElement;
    button.textContent = text;
    button.addEventListener('click', onClick);
    return button;
  }

  private createHeader(text: string): HTMLDivElement {
    const header = createElementWithClass('div', 'headers') as HTMLDivElement;
    header.textContent = text;
    return header;
  }

  private createContainer(elements: HTMLElement[]): HTMLDivElement {
    const container = createElementWithClass('div', 'ndp-button-container');
    elements.forEach(element => container.appendChild(element));
    return <HTMLDivElement>container;
  }

  private _onGoToFileManagerButtonClick(event: Event): void {
    this.commands.execute('filebrowser:activate');
  }

  private _onGoToGitExtensionButtonClick(event: Event): void {
    this.commands.execute('git:ui');
  }

  private async _onButtonDownloadClick(event: Event): Promise<void> {
    this.spinner.style.display = 'block';
    const currentPath = this.fileBrowser.model.path;
    const selectedDataset = this.dropdownDatasets.value;

    try {
      const data = await requestAPI<any>('download_datasets', {
        method: 'POST',
        body: JSON.stringify({ path: currentPath, dataset: selectedDataset })
      });
      console.log(data);
    } catch (reason) {
      console.error(
        `The ndp_jupyterlab_extension server extension appears to be missing.\n${reason}`
      );
    } finally {
      this.spinner.style.display = 'none';
    }
  }

  private async _onButtonInstallClick(event: Event): Promise<void> {
    this.spinner.style.display = 'block';
    const currentPath = this.fileBrowser.model.path;

    try {
      const data = await requestAPI<any>('install_requirements', {
        method: 'POST',
        body: JSON.stringify({ path: currentPath })
      });
      console.log(data);
    } catch (reason) {
      console.error(
        `The ndp_jupyterlab_extension server extension appears to be missing.\n${reason}`
      );
    } finally {
      this.spinner.style.display = 'none';
    }
  }

  private async _getWorkspacesData(): Promise<void> {
    try {
      this.workspaces = await fetchWorkspaces();
      this.updateWorkspacesDropdown();
    } catch (error) {
      console.error('Error fetching workspaces:', error);
    }
  }

  private async _onButtonGetWorkspacesDataClick(event: Event): Promise<void> {
    this.spinner.style.display = 'block';
    try {
      await this._getWorkspacesData();
    } catch (reason) {
      console.error(
        `The ndp_jupyterlab_extension server extension appears to be missing.\n${reason}`
      );
    } finally {
      this.spinner.style.display = 'none';
    }
  }

  private updateWorkspacesDropdown(): void {
    this.dropdownWorkspaces.innerHTML = '';
    const sortedWorkspaces = this.workspaces.sort((a, b) =>
      a.workspace_name.localeCompare(b.workspace_name)
    );

    sortedWorkspaces.forEach(workspace => {
      const option = document.createElement('option');
      option.value = workspace.workspace_name;
      option.textContent = workspace.workspace_name;
      this.dropdownWorkspaces.appendChild(option);
    });

    this.updateDatasetsDropdown();
  }

  private updateDatasetsDropdown(): void {
    const selectedWorkspaceName = this.dropdownWorkspaces.value;
    const selectedWorkspace = this.workspaces.find(
      workspace => workspace.workspace_name === selectedWorkspaceName
    );

    if (selectedWorkspace) {
      this.dropdownDatasets.innerHTML = '';
      const sortedDatasets = selectedWorkspace.datasets.sort((a, b) =>
        a.localeCompare(b)
      );

      sortedDatasets.forEach(dataset => {
        const option = document.createElement('option');
        option.value = dataset;
        option.textContent = dataset;
        this.dropdownDatasets.appendChild(option);
      });
    }
  }

  private updatePathDisplay(path?: string): void {
    const currentPath = path || this.fileBrowser.model.path;
    this.pathDisplay.textContent =
      currentPath === '' ? 'root/' : `root/${currentPath}`;
  }
}
